<template>
  <div class="confirm" v-bind:style="{minHeight: win_height + 'px'}">
    <audio :src="mp3" ref="music" preload></audio>
    <div class="bg" ref="bg">
      <span class="img" data-depth="0.50" :class="{active: isTrue}" :style="{backgroundImage: 'url(' + img +')'}"></span>
    </div>
    <div class="top">
      <div class="left" @click="music()"><i class="icon-vioce"></i>听音频猜明星</div>
      <div class="right">求助好友<i class="icon-mark"></i></div>
    </div>
    <i class="next" v-if="!isTrue"></i>
    <i class="next active" v-else @click="goNext()"></i>
    <div class="bottom">
      <div class="anwser">
        <div class="a-t"><span v-for="anwser in anwsers">{{ anwser }}</span></div>
        <div class="a-b">{{ num }}/{{ total }}</div>
      </div>
      <div class="icon-logo"></div>
    </div>
    <div class="fly" v-show="!isTrue" ref="scene" id="scene">
      <span 
          @click="getAnwser(name)" 
          v-for="name in names" 
          :class="{active: isClick}"
          :style="{ transform: 'translate3d('+ gamma +'px,'+ beta +'px,' + 0 +')' + 'rotate('+ rotate +'deg)' }">{{ name }}</span>
    </div>
  </div>
</template>

<script>
import { info } from '../mUtil/info';
import { randomArr } from '../mUtil/mutil';

export default {
  name: 'confirm',
  data() {
    return {
      win_height: window.innerHeight,
      img: info[0].img, // 背景图片
      anwsers: [], // 答案
      isTrue: 0, // 是否正确
      mp3: info[0].mp3, // 背景音乐
      names: info[0].names, // 文字
      right: info[0].right, // 正确答案
      globalI: 0,
      anwserI: 0,
      isClick: 0,
      total: info.length, // 总题数
      isFinish: 0,
      num: 1, // 当前题数
      alpha: 0,
      beta: 0,
      gamma: 0,
      rotate: 0,
    };
  },
  mounted() {
    const that = this;
    that.anwsers = []; // 添加占位符
    for (let i = 0; i < that.right.length; i += 1) {
      that.anwsers.push('');
    }
    that.img = info[that.globalI].img;
    that.mp3 = info[that.globalI].mp3;
    that.names = randomArr(info[that.globalI].names);
    that.right = info[that.globalI].right;
    setTimeout(() => {
      this.$refs.music.play();
    }, 1000);
    window.addEventListener('deviceorientation', (obj) => {
      const beta = obj.beta;
      const gamma = obj.gamma;
      if (Math.abs(beta) < 45) {
        that.beta = beta;
      }
      if (Math.abs(gamma) < 45) {
        that.gamma = gamma;
      }
    }, false);
  },
  methods: {
    getAnwser(name) {
      const that = this;
      if (that.anwserI < that.right.length) {
        that.anwsers.splice(that.anwserI, 1, name);
        that.anwserI += 1;
        if (that.anwsers.toString() === that.right.toString()) {
          that.isTrue = 1;
        }
        if (that.globalI === that.right.length) {
          that.isFinish = 1;
        }
      }
    },
    music() {
      this.$refs.music.play();
    },
    goNext() {
      const that = this;
      that.isTrue = 0;
      that.isFinish = 0;
      that.anwserI = 0;
      that.anwsers = []; // 添加占位符
      if (that.globalI < info.length - 1) {
        that.globalI += 1;
        that.img = info[that.globalI].img;
        that.mp3 = info[that.globalI].mp3;
        that.names = randomArr(info[that.globalI].names);
        that.right = info[that.globalI].right;
      }
      for (let i = 0; i < that.right.length; i += 1) {
        that.anwsers.push('');
      }
      this.$refs.music.load();
      setTimeout(() => {
        this.$refs.music.play();
      }, 1000);
      if (that.num < info.length) {
        that.num += 1;
      }
    },
  },
};
</script>

<style scoped lang="less">
.confirm {
  position: relative;
}
.bg {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  .img {
    // background-image: url(../assets/img1.jpg);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: 100%;
    filter: blur(30px);
    transition: all 1s ease-in;
    &.active {
      filter: blur(0);
    }
  }
  img {
    min-width: 100%;
    height: 100%;
  }
}
.top {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  color: #54fa00;
  border-bottom: 2px solid #54fa00;
  font-size: 14px;
  justify-content: space-between;
  align-items: center;
  margin: 0 1rem;
  padding-top: 0.8rem;
  padding-bottom: 0.1rem;
  z-index: 99;
  .icon-vioce {
    display: inline-block;
    background: url(../assets/icon-voice.png) no-repeat center center / 100% 100%;
    width: 43px;
    height: 31.5px;
    vertical-align: middle;
    margin-right: 5px;
    position: relative;
    top: -4px;
  }
  .icon-mark {
    display: inline-block;
    background: url(../assets/mark.png) no-repeat center center / 100% 100%;
    width: 30px;
    height: 29px;
    vertical-align: middle;
    margin-left: 5px;
    position: relative;
    top: -4px;
  }
}
.bottom {
  display: flex;
  color: #54fa00;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  justify-content: space-between;
  background: rgba(0,0,0,0.5);
  padding: 0.5rem;
  align-items: center;
  font-size: 15px;
  .anwser {
    .a-t {
      border-bottom: 1px solid #54fa00;
      text-align: center;
      margin-bottom: 0.3rem;
      padding: 0 0.1rem;
    }
    span {
      display: inline-block;
      width: 20px;
      padding: 0 5px;
      &:after {
        content: '/';
      }
      &:last-child {
        &:after {
          display: none;
        }
      }
    }
  }
  .icon-logo {
    display: inline-block;
    background: url(../assets/logo.png) no-repeat center center / 100% 100%;
    width: 149px;
    height: 41.5px;
  }
}
.fly {
  color: #fff;
  position: absolute;
  top: 3rem;
  right: 1rem;
  bottom: 3rem;
  left: 1rem;
  z-index: 9;
  font-size: 20px;
  span {
    margin: 5px;
    padding: 5px;
    font-family: 'sukai';
    position: absolute;
    transition: all 0.1s ease;
    z-index: 99;
    &:first-child {
      left: 0rem;
      top: 4rem;
    }
    &:nth-child(2) {
      left: 1.7rem;
      top: 1.7rem;
    }
    &:nth-child(3) {
      left: 4rem;
      top: 0;
    }
    &:nth-child(4) {
      left: 7rem;
      top: 4rem;
    }
    &:nth-child(5) {
      left: 9rem;
      top: 10rem;
    }
    &:nth-child(6) {
      left: 11rem;
      top: 6rem;
    }
    &:nth-child(7) {
      left: 6rem;
      top: 10rem;
    }
    &:nth-child(8) {
      left: 10rem;
      top: 14rem;
    }
    &:nth-child(9) {
      left: 11rem;
      top: 3rem;
    }
    &:nth-child(10) {
      left: 3rem;
      top: 18rem;
    }
    &:nth-child(11) {
      left: 9rem;
      top: 17rem;
    }
    &:nth-child(12) {
      left: 2rem;
      top: 16rem;
    }
    &:nth-child(13) {
      left: 7rem;
      top: 12rem;
    }
    &:nth-child(14) {
      left: 3rem;
      top: 11rem;
    }
    &:nth-child(15) {
      left: 0;
      top: 13rem;
    }
    &:nth-child(16) {
      left: 6rem;
      top: 14rem;
    }
    &:nth-child(17) {
      left: 3rem;
      top: 8rem;
    }
    &:nth-child(18) {
      left: 1rem;
      top: 6rem;
    }
    &:nth-child(19) {
      left: 4rem;
      top: 4rem;
    }
    &:nth-child(20) {
      left: 8rem;
      top: 2rem;
    }
    &:nth-child(21) {
      left: 10rem;
      top: 1rem;
    }
    &:nth-child(22) {
      left: 5rem;
      top: 7rem;
    }
    &:nth-child(23) {
      left: 7rem;
      top: 8rem;
    }
    &.active {
      color: #54fa00;
    }
  }
}
.next {
  background-image: url(../assets/next.png);
  background-size: 89.5px 23.5px;
  position: absolute;
  width: 89.5px;
  height: 23.5px;
  top: 2.6rem;
  right: 1rem;
  z-index: 99;
  &.active {
    background-image: url(../assets/next-active.png);
  }
}
</style>



